import React from 'react';
import './App.css';
import { Map, GoogleApiWrapper, Marker ,InfoWindow} from 'google-maps-react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Button } from 'react-bootstrap';


const mapStyles = {
  width: '100%',
  height: '100%'
};



export class App extends React.Component {

  constructor() {
    super();
    this.state = { show: true, min: 0, max: 0, info: [0, 0] }
  }

  route = () => {
    fetch("http://freakh2o.pythonanywhere.com/?min=" + this.state.min + "&max=" + this.state.max)
      .then(response => response.json())
      .then(data => this.setState({ info: data, show: false }));
  }



  handleMin = (e) => {
    console.log(e.target.value)
    this.setState({ min: e.target.value })
  }
  handleMax = (e) => {
    this.setState({ max: e.target.value })
  }

  onMarkerClick = (props, marker, e) =>
  this.setState({
    activeInfo:props.info,
    img:props.src,
    activeMarker: marker,
    showingInfoWindow: true
  });


  Displayable = () => {
    if (this.state.show === true) {
      return <div>
        <h1>FOOD ROUTER</h1>
        <input id="min" onChange={this.handleMin} placeholder="Enter Min Price" style={{ marginTop: "20px" }} />
        <br></br>
        <input id="max" onChange={this.handleMax} placeholder="Enter Max Price" style={{ marginTop: "20px" }} />
      </div>
    }
    else {
      return <div> 
        < Map  google={this.props.google} zoom={14} style={mapStyles} initialCenter={{lat: 37.778519,lng: -122.405640}}>
        {this.state.info.map((marker)=>{
         return <Marker
          onClick={this.onMarkerClick}
          src={marker.src}
          info={marker.info}
          title={marker.title}
          name={marker.name}
          position={marker.location}
          />
        })}


        <Marker
          title={'The marker`s title will appear as a tooltip.'}
          info={'RESTAURANT 1'}
          onClick={this.onMarkerClick}
          position={{ lat: 37.778519, lng: -122.4088640 }} />
          <InfoWindow
          marker={this.state.activeMarker}
          visible={this.state.showingInfoWindow}>
            <div>
              <h4>{this.state.activeInfo}</h4>
              <img style={{width:"350px",height:"350px"}} src={this.state.img}></img>
            </div>
        </InfoWindow>
          
      </ Map>


      </div>
    }

  }



  render() {
    return (
      <div className="App">
        <this.Displayable></this.Displayable>
        <br></br>
        <Button onClick={this.route} variant="dark">ROUTE</Button>
      </div>
    );

  }

}



export default GoogleApiWrapper({
  apiKey: 'AIzaSyC6BrTtbUsJxdWJuRPI99wOs_6hXa_WhdU'
})(App);
